/* ------------------------------------------------------------------ */
/* REGLAGES DE BASE */
/* ------------------------------------------------------------------ */

* {
  margin: 0;
  padding: 0!;
  box-sizing: border-box;
}

/* ------------------------------------------------------------------ */
/* ------------------------------------------------------------------ */
.test{
background-color: #eceff1 ;

}

/* ------------------------------------------------------------------ */
/* HEADER */
/* ------------------------------------------------------------------ */
.navbar-light{
width: 100%;
box-shadow: 1px 1px 5px #555;
height: 45px;

}
.navbar-nav i {
width: 1em;
}


/* ------------------------------------------------------------------ */
/*FORM                                                                */
/* ------------------------------------------------------------------ */


.map{
height: 100vh;
width: 100%;
}
/* ------------------------------------------------------------------ */
/* MENU VERTICAL */
/* ------------------------------------------------------------------ */


.container{
/*Création dimension du container qui contient la sidebar*/
width: 100%; 
height: 100%;
margin-left: -15px; /*Pour coller le menu à gauche de l'écran*/
margin-top : 10px;

}


.my-sidebar {
/*Dimension de la sidebar*/

position: fixed; /*Fixer le menu vertical*/
width: 165px; /*On définis la longueur */

height: 100%; /*Et la hauteur */

background-color: #88a2bb;

padding: 0;
border-radius: 5px;


margin: 0;

}
.navbar-nav #en-cours p{
background-color: rgba(59, 59, 59, 0.158);
}
.navbar-nav  p:hover{
text-decoration: underline;
color: rgb(0, 0, 0);
background-color: rgba(59, 59, 59, 0.158);

}

.my-sidebar a {
 /*Afficher les éléments l'un en dessous de l'autre*/
/*color: #626567*/
color: rgb(255, 255, 255);


}


.navbar-brand {
  font-size: 25px; /*Taille du texte*/
font-weight: 600;
text-transform: uppercase;
margin : 0;
}



.brand{
font-size: 14px; /*Taille du titre du menu vertical */
}

.navbar-nav p{
margin : 0;
font-size: 25px; /*Taille du texte*/
font-weight: 600;
text-transform: uppercase; /*Mets tous en majuscule*/
width: 100%;
display: block;
line-height: 115px;
border-top : 3px solid #f8f9fa
}
.navbar-nav{
padding: 0;
margin: 0;
}

.navbar-nav #export p{
  border-bottom:3px solid #f8f9fa; 
}

/* ------------------------------------------------------------------ */
/* ------------------------------------------------------------------ */




/* ------------------------------------------------------------------ */
/* LEGENDE */
/* ------------------------------------------------------------------ */

.legende{
